<template>
  <swiper :options="swiperOption" ref="swiper">
    <div class="ido-scroll-pull-down" v-if="pullDown">
      <ido-loading :text="pullDownText" inline ref="pullDownLoading"></ido-loading>
    </div>
    <swiper-slide>
      <slot></slot>
    </swiper-slide>
    <div class="ido-scroll-pull-up" v-if="pullUp">
      <ido-loading :text="pullUpText" inline ref="pullUpLoading"></ido-loading>
    </div>
    <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
  </swiper>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import IdoLoading from '../../ido-loading/src/ido-loading'
import { PULL_DOWN_HEIGHT, PULL_DOWN_TEXT_INIT, PULL_DOWN_TEXT_START, PULL_DOWN_TEXT_ING, PULL_DOWN_TEXT_END, PULL_UP_HEIGHT, PULL_UP_TEXT_INIT, PULL_UP_TEXT_START, PULL_UP_TEXT_ING, PULL_UP_TEXT_END } from './config'

export default {
  name: 'IdoScroll',
  components: {
    swiper,
    swiperSlide,
    IdoLoading
  },
  props: {
    scrollbar: {
      type: Boolean,
      default: true
    },
    // 
    data: {
      type: [Array, Object]
    },

    pullDown: {
      type: Boolean,
      default: false
    },
    pullUp: {
      type: Boolean,
      default: false
    }
  },

  watch: {
    // 
    data() {
      this.update()
    }
  },

  methods: {
    /** APIS */
    update() {
      this.$nextTick(() => {
        this.$refs.swiper && this.$refs.swiper.swiper.update()
      })
    },
    // 
    scrollToTop(speed, runCallbacks) {
      this.$refs.swiper && this.$refs.swiper.swiper.slideTo(0, speed, runCallbacks)
    },
    init() {
      // data
      // 
      this.pulling = false
      this.pullDownText = PULL_DOWN_TEXT_INIT
      this.pullUpText = PULL_DOWN_TEXT_INIT
      this.swiperOption = {
        direction: 'vertical',
        // V cnm v
        //  slidesPerView: 'auto'  */
        slidesPerView: 'auto',
        freeMode: true,
        setWrapperSize: true,
        scrollbar: {
          el: this.scrollbar ? '.swiper-scrollbar' : null,
          hide: true // 
        },
        on: {
          // 
          sliderMove: this.scroll,
          // 
          touchEnd: this.touchEnd,
          transitionEnd: this.scrollEnd // scrollEnd
        }
      }
    },
    touchEnd() {
      if (this.pulling) {
        //  
        return
      }
      const swiper = this.$refs.swiper.swiper

      // 100
      if (swiper.translate > PULL_DOWN_HEIGHT) {
        if (!this.pullDown) {
          return
        }
        this.pulling = true

        // 
        swiper.allowTouchMove = false // 
        swiper.setTransition(swiper.params.speed) // 
        swiper.setTranslate(PULL_DOWN_HEIGHT) // 100
        swiper.params.virtualTranslate = true // 
        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING)
        this.$emit('onRefresh', this.pullDownEnd)
      } else if (swiper.isEnd) {
        // 
        if (!this.pullUp) {
          return
        }

        const totalHeight = parseInt(swiper.$wrapperEl.css('height'))
        const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > totalHeight
        if (isPullUp) {
          // 
          this.pulling = true
          swiper.allowTouchMove = false // 
          swiper.setTransition(swiper.params.speed)
          swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height))
          swiper.params.virtualTranslate = true // 
          this.$refs.pullUpLoading.setText(PULL_UP_TEXT_ING)
          this.$emit('onLoadmore', this.pullUpEnd)
        }
      }
    },

    scrollEnd() {
      const swiper = this.$refs.swiper.swiper

      // scrollEnd 
      this.$emit('onScrollEnd', swiper.translate, swiper, this.pulling)
    },
    pullDownEnd() {
      this.pulling = false

      const swiper = this.$refs.swiper.swiper
      this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END)
      swiper.params.virtualTranslate = false
      swiper.allowTouchMove = true
      swiper.setTransition(swiper.params.speed)
      swiper.setTranslate(0)

      setTimeout(() => {
        // 
        this.$emit('pullDownTransitionEnd')
      }, swiper.params.speed)
    },
    pullUpEnd() {
      const swiper = this.$refs.swiper.swiper
      this.pulling = false
      this.$refs.pullUpLoading.setText(PULL_UP_TEXT_END)
      swiper.params.virtualTranslate = false
      swiper.allowTouchMove = true
      // const totalHeight = parseInt(swiper.$wrapperEl.css('height'))
      // swiper.setTransition(swiper.params.speed)
      // swiper.setTranslate(-(totalHeight - swiper.height))

      // setTimeout(() => {
      //   // 
      //   this.$emit('pullDownTransitionEnd')
      // }, swiper.params.speed)
    },
    scroll() {
      const swiper = this.$refs.swiper.swiper

      // scroll
      this.$emit('onScroll', swiper.translate, this.$refs.swiper.swiper)
      if (this.pulling) {
        //  
        return
      }

      if (swiper.translate > 0) {
        if (!this.pullDown) {
          // 
          return
        }

        let percent = swiper.translate / 100
        // if (percent <= 1) {
        this.percent = Number(percent)
        // }

        // 
        if (swiper.translate > PULL_DOWN_HEIGHT) {
          this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START)
        } else {
          this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT)
        }
      } else if (swiper.isEnd) {
        // 
        if (!this.pullUp) {
          return // 
        }

        // 
        const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > parseInt(swiper.$wrapperEl.css('height'))
        if (isPullUp) {
          this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START)
        } else {
          this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT)
        }
      }
    }
  },
  created() {
    this.init()
  }
}
</script>

<style lang="scss" scoped>
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  height: auto;
}

.ido-scroll-pull-up,
.ido-scroll-pull-down {
  position: absolute;
  left: 0;
  //  
  width: 100%;
}
.ido-scroll-pull-down {
  bottom: 100%;
  height: 80px;
}

.ido-scroll-pull-up {
  top: 100%;
  height: 30px;
}
.box {
  height: 800px;
  width: 100%;
  background-color: skyblue;
}
</style>
