import IdoScroll from './ido-scroll/src/ido-scroll.vue'
import IdoLoading from './ido-loading/src/ido-loading.vue'

// 
const components = [IdoScroll, IdoLoading]

//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  })
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  // 
  IdoScroll,
  IdoLoading
}
